* {
    text-decoration-line: none;
}

a {
    text-decoration: none;
    color: #7ab9e7;
    font-weight: bold;
}

a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus {
    text-decoration: none;
}

a:hover,
a:active {
    text-decoration: none;
}

.container {
    margin: auto;
    display: flex;
    flex-direction: column;
}

.haveAcc {
    text-align: right;
    margin: 1%;
}

.headH {
    margin-top: 3.5%;
    text-align: center;

}

.form {
    width: 30%;
    margin: auto;
    margin-top: 2%;
    font-weight: bold;
}

/*중간에 두개가 한줄에 들어가는 부분*/
.middle {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2%;
}

.inputBox {
    width: 100%;
    min-width: 270px;
    height: 40px;
    margin-top: 1%;
    margin-bottom: 5%;
    padding-left: 2%;
}

.register {
    margin-top: 5%;
    width: 100%;
    font-size: larger;
    height: 60px;
    color: white;
    font-weight: bolder;
    cursor: pointer;
    background-color: #7ab9e7;
    border: none;
    border-radius: 3px;
}

.gender {
    display: flex;
    width: 100%;
    height: 40px;
    min-width: 270px;
    margin-top: 1%;
    margin-bottom: 5%;
}

.select {
    display: flex;
    width: 100%;
    margin: auto;
    height: 100%;
    background-color: white;
    border: 2px solid #7ab9e7;
    text-align: center;
    align-items: center;
    justify-content: center;
    cursor: pointer;


}

.selected {
    display: flex;
    color: white;
    width: 100%;
    margin: auto;
    height: 100%;
    background-color: #7ab9e7;
    text-align: center;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}